.box.loading:before {
  content: "";
  border-top: 3px solid #fff;
  border-right: 3px solid #fff;
  width: 0;
  height: 0;
  animation: 3s linear infinite loading-top;
  position: absolute;
  top: -2px;
  left: 0;
}

.box.loading:after {
  content: "";
  border-bottom: 3px solid #fff;
  border-left: 3px solid #fff;
  width: 0;
  height: 0;
  animation: 3s linear infinite loading-bottom;
  position: absolute;
  bottom: -2px;
  right: 0;
}

@keyframes loading-top {
  0% {
    width: 0;
    height: 0;
  }

  25% {
    width: 286px;
    height: 0;
  }

  50% {
    width: 286px;
    height: 286px;
  }

  100% {
    width: 286px;
    height: 286px;
  }
}

@keyframes loading-bottom {
  0% {
    opacity: 0;
    width: 0;
    height: 0;
  }

  50% {
    opacity: 0;
    width: 0;
    height: 0;
  }

  50.1% {
    opacity: 1;
    width: 0;
    height: 0;
  }

  75% {
    opacity: 1;
